@color_blue:#3aabf7;
@color_fff:#fff;
@color_bluedark:#2e8ece;
.navbar-inverse .navbar-brand{
    color:@color_fff;
    font-size: 26px;
    padding: 10px 15px;
}
.navbar-inverse .navbar-nav > li > a{
    color:@color_fff;
}
.navbar-inverse .navbar-nav > li > a:hover{
    color:@color_blue;
    text-decoration: underline;
}
.navbar:nth-of-type(1){
    border-radius:0;
    margin-bottom: 0;
}
.navbar-default .navbar-nav li a{
    line-height: 46px;
    font-size: 16px;
}
.navbar-default .navbar-nav>li>a:hover{
    color:@color_bluedark;
    background-color: #e7e7e7;
}
//项目特色
.el-feature{
    height: 80px;
    margin-top: 20px;
    overflow: hidden;
}
.el-feature>dl{
    width: 33%;
    padding-left: 80px;
    float: left;
}
.el-feature .el-feature-item1{
    background: url(../../../../p2p/images/feature01.png) no-repeat;
}
.el-feature .el-feature-item2{
    background: url(../../../../p2p/images/feature02.png) no-repeat;
}
.el-feature .el-feature-item3{
    background: url(../../../../p2p/images/feature03.png) no-repeat;
}
.el-feature>dl>dt{
    font-size: 18px;
    margin-bottom: 10px;
}
.el-feature>dl>dd{
    color:#999;
}
